













































import Vue, {VueConstructor, PropOptions} from 'vue';
import {Nullable} from '@/common/utils/types';
import RoomleEmbeddingApi, {RoomleConfiguratorApi, RoomleViewerApi} from '@/configurator/embedding/roomle-configurator-api';

export default (Vue as VueConstructor<Vue & {
  $refs: {
    glb: HTMLElement,
    configurator: HTMLElement,
  },
}>).extend({
  props: {
    defaultOptions: Object,
    configuratorId: String,
    embeddingApi: Function as PropOptions<typeof RoomleEmbeddingApi>,
  },
  data() {
    return {
      configuratorApi: null as Nullable<RoomleConfiguratorApi>,
      viewerApi: null as Nullable<RoomleViewerApi>,
      isConfigurator: false,
      isGlbViewer: false,
    };
  },

  methods: {
    async loadObject(id: string, isViewer: boolean) {
      const api = (!isViewer) ? this.configuratorApi : this.viewerApi;
      if (!api) {
        if (!this.embeddingApi) {
          console.error('emedding api not ready yet');
          return;
        }
        const options = {id};
        const merged = {...this.defaultOptions, ...options};
        delete merged.isItem;
        if (isViewer) {
          const apiInstance = await this.embeddingApi.createViewer(this.configuratorId, this.$refs.glb, merged);
          this.viewerApi = apiInstance;
        } else {
          const apiInstance = await this.embeddingApi.createConfigurator(this.configuratorId, this.$refs.configurator, merged);
          this.configuratorApi = apiInstance;
        }
      } else {
        await api.ui.loadObject(id);
      }
      this.isConfigurator = !isViewer;
      this.isGlbViewer = isViewer;
    },
  },

});
