
























































































































































import Vue, {VueConstructor} from 'vue';
import {Nullable} from '@/common/utils/types';
import RoomleEmbeddingApi, {RoomleConfiguratorApi} from '@/configurator/embedding/roomle-configurator-api';
import GlbConfSwitchSecret from '@/configurator/embedding/example/GlbConfSwitchSecret.vue';
import GlbConfSwitch from '@/configurator/embedding/example/GlbConfSwitch.vue';
import {UI_BUTTON} from '@/configurator/embedding/types';
import {ExposedApi} from '@/configurator/embedding/exposed-api';

export default (Vue as VueConstructor<Vue & {
  $refs: {
    rml1: HTMLElement,
    rml2: HTMLElement,
    rml3: HTMLElement,
    rml4: HTMLElement,
    rml5: HTMLElement,
    rml6: HTMLElement,
    rml7: HTMLElement,
    rml8: HTMLElement,
    rml9: HTMLElement,
  },
}>).extend({
  components: {
    GlbConfSwitch,
    GlbConfSwitchSecret,
  },
  data() {
    const showConfigurators = true;
    return {
      showConfigurators: {
        number01: showConfigurators,
        number02: showConfigurators,
        number03: showConfigurators,
        number04: showConfigurators,
        number05: showConfigurators,
        number06: showConfigurators,
        number07: showConfigurators,
        number08: showConfigurators,
        number09: showConfigurators,
        number10: showConfigurators,
        number11: showConfigurators,
        number12: showConfigurators,
        number13: showConfigurators,
        number14: showConfigurators,
      },
      embeddingApi: null as Nullable<typeof RoomleEmbeddingApi>,
      configuratorId: 'demoConfigurator',
      defaultOptions: {
        id: 'usm:frame',
        // initialFloorMaterial: 'koinor:EicheLondon',
        buttons: {[UI_BUTTON.ADDONS]: false},
        useLocalRoomle: true,
        // customApiUrl: 'https://api.test.roomle.com/v2',
      },
    };
  },
  computed: {
    simpleLink(): string {
      const url = this.regularLink;
      const separator = url.includes('?') ? '&' : '?';
      return url + separator + 'api=false';
    },
    regularLink(): string {
      const url = window.location.href;
      const replacer = url.includes('roomle.gitlab.io') ? 'index.html' : '';
      return url.replace('embedding.html', replacer);
    },
  },
  async mounted() {
    const {default: RoomleConfigurator} = await import('@/configurator/embedding/roomle-configurator-api');
    this.embeddingApi = RoomleConfigurator;
    if (!this.embeddingApi) {
      return; // keep tslint happy
    }
    const configuratorId = this.configuratorId;
    const useLocalRoomle = true;

    const defaultOptions = this.defaultOptions;

    if (this.showConfigurators.number01) {
      const options1 = {...defaultOptions, locale: 'fr'};
      this.embeddingApi.createConfigurator(configuratorId, this.$refs.rml1, options1).then((interface1) => {
        interface1.ui.setPrice('â‚¬', 3133.7);
        interface1.ui.callbacks.onRequestProduct = (configurationId, image, partlist, price, labels, configuration) => {
          console.log(configurationId, image, partlist, price, labels, configuration);
        };
        // tslint:disable-next-line:only-arrow-functions
        interface1.analytics.callbacks.onGATracking = function () {
          console.log('GA outside');
          console.log(...arguments);
        };
        // interface1.ui.callbacks.onUseFullPage = () => console.log('use full page');
        document.getElementById('start-configure')!.addEventListener('click', () => {
          console.log('startConfiguring');
          interface1.ui.startConfiguring();
        });
        document.getElementById('trigger-request')!.addEventListener('click', async () => {
          console.log('trigger-request');
          await interface1.ui.triggerRequestProduct();
          console.log('trigger-request-done');
        });

        document.getElementById('load-configuration-string')!.addEventListener('click', async () => {
          console.log('load-configuration-string');
          await interface1.ui.loadConfigurationString('{"componentId": "usm:frame"}');
          console.log('load-configuration-string-done');
        });
        console.log(interface1);
      });
    }

    if (this.showConfigurators.number02) {
      const options2 = {
        id: 'isdt:Geraeteschrank_Master:3EA8855EABD278A70446FCA0CF445F1B301A6ACC8D1D2D3481B4C8C25280141D',
        useLocalRoomle,
      };
      this.embeddingApi.create(configuratorId, this.$refs.rml2, options2).then((interface2) => {
        interface2.ui.callbacks.onRequestProduct = (num) => console.log('clicked on request Product with ', num) as any;
        interface2.ui.callbacks.onButtonClicked = (name) => {
          console.log('Button clicked: ' + name);
        };
        console.log(interface2);
      });
    }

    if (this.showConfigurators.number03) {
      const options3 = {
        usePriceService: true,
        id: 'usm_gewinnspiel:frame',
        useLocalRoomle,
        buttons: {[UI_BUTTON.STARTCONFIGURE]: false},
      };
      this.embeddingApi.create('usmhaller', this.$refs.rml3, options3).then(async (interface3) => {
        console.log(interface3);
        // tslint:disable-next-line:only-arrow-functions
        interface3.extended.callbacks!.onPartListUpdate = function () {
          console.log(...arguments);
        };
        // tslint:disable-next-line:only-arrow-functions
        interface3.ui.callbacks!.onPartListUpdate = function () {
          console.log('UI', ...arguments);
        } as any;
      });
    }
    if (this.showConfigurators.number04) {
      const options4 = {
        id: 'visplay:qubo_imm_master:22ED95680CA820720987E117DF856CBB0566F2A6ADA8C9800D88FF15C5D174CE',
        isItem: false,
        initialFloorMaterial: 'koinor:EicheLondon',
        buttons: {[UI_BUTTON.ADDONS]: false, [UI_BUTTON.EXPORT_3D]: true},
        useLocalRoomle,
        skin: {
          'primary-color': '#1d68bd',
        },
      };
      this.embeddingApi.create(configuratorId, this.$refs.rml4, options4).then((interface4) => {
        const button = document.getElementById('conf4-button');
        button!.addEventListener('click', () => interface4.ui.startConfiguring());
        interface4.ui.callbacks.onResize = (isDesktop) => {
          console.log('receive callback onResize', {isDesktop});
          if (isDesktop) {
            button!.style.display = 'none';
          } else {
            button!.style.display = 'block';
          }
        };
        console.log(interface4);
      });
    }
    if (this.showConfigurators.number05) {
      const options5 = {
        id: 'usm:frame',
        useLocalRoomle,
      };
      const buttonDirectOpen = document.getElementById('conf5-button');
      let interface5: Nullable<RoomleConfiguratorApi> = null;
      buttonDirectOpen!.addEventListener('click', () => {
        if (interface5) {
          interface5.ui.startConfiguring();
          return;
        }
        if (!this.embeddingApi) {
          return; // keep tslint happy
        }
        this.embeddingApi.create(configuratorId, this.$refs.rml5, options5).then((iface) => {
          interface5 = iface;
          interface5.ui.startConfiguring();
        });
      });
    }
    if (this.showConfigurators.number06) {
      const options6 = {...defaultOptions};
      try {
        await (RoomleConfigurator as any).create(this.$refs.rml1, options6);
      } catch (e) {
        console.log('This error is on purpose, it should be a miss-match of data type (id !== string)');
        console.log(e);
      }
      try {
        await (RoomleConfigurator as any).create(this.$refs.rml6, options6);
        await (RoomleConfigurator as any).create(this.$refs.rml6, options6);
      } catch (e) {
        console.log('This error is on purpose, it should throw because DOM element already in use');
        console.log(e);
      }
    }
    if (this.showConfigurators.number07) {
      const options7 = {
        ...defaultOptions, variants: {
          'furniture_eservice:regalsystem': 'DB_Start_ohne_Element',
        },
        id: 'musterring:lucia_element:75417430ECE01258E16B2972186CAA869FC11FB01E7C66370464AF3C56D2FFDB',
      };
      this.embeddingApi.create('roomleOnly', this.$refs.rml7, options7).then((iface) => {
        iface.ui.startConfiguring();
      });
    }
    if (this.showConfigurators.number08) {
      const options8 = {...defaultOptions, id: 'biohort:europa', isItem: true};
      this.embeddingApi.create(configuratorId, this.$refs.rml8, options8).then((iface) => {
        document.getElementById('conf8-button')!.addEventListener('click', async () => {
          const wellKnownParamKey = 'preferredDoorType';
          // fetch current parameters of the whole object
          const params = await iface.extended.getParametersOfRootComponent();
          // filter for the parameter which we are interested in
          const foundParam = params.find((param) => param.key === wellKnownParamKey);
          if (!foundParam) {
            console.warn('Did not find param "' + wellKnownParamKey + '", did content change?');
            return;
          }
          // save the old value so we can reset after generation of images
          const oldVal = foundParam.value;
          // set parameter to a well known value
          await iface.extended.setParameterOfRootComponent(foundParam, '2');
          // generate images
          const topImage = await iface.extended.prepareTopImage(true);
          console.log(topImage.image);
          // reset parameter to old value
          await iface.extended.setParameterOfRootComponent(foundParam, oldVal);
        });
        iface.ui.startConfiguring();
      });
    } else {
      const warning = document.createElement('div');
      warning.innerText = 'To use this demo set showConfigurators.number08 to true';
      this.$refs.rml8.appendChild(warning);
    }

    if (this.showConfigurators.number12) {
      const iframe = this.$el.querySelector('#fork-iframe') as HTMLIFrameElement;
      const rubensAPI = await this.getRubensApi(iframe!);
      const priceDataBase: any = {};
      rubensAPI.callbacks.onPartListUpdate = (partList: any) => {
  console.log('fd');      const priceSum = partList.reduce((sum: any, part: any) => {
          if (!priceDataBase[part.articleNr]) {
            priceDataBase[part.articleNr] = Math.random() * 10;
          }
          return sum += priceDataBase[part.articleNr];
        }, 0);
        const shippingCosts = 30;
        // Tell the Roomle Configurator to show the current price
        rubensAPI.setPrice('â‚¬', priceSum + shippingCosts);
      };
    }
    if (this.showConfigurators.number13) {
      const options13 = {
        ...defaultOptions,
        id: 'roomle_content_demo:kitchen001',
      };
      this.embeddingApi.create('noActivePackage', this.$refs.rml9, options13);
    }
    if (this.showConfigurators.number14) {
      const options14 = {
        ...defaultOptions,
        moc: true,
        id: 'cnc15wy0w8occbmol1ms927lbnrsr2ig',
        catalogRootId: 'moc_sample_catalog',
      };
      this.embeddingApi.createPlanner(configuratorId, this.$refs.rml10 as HTMLElement, options14).then((interface14) => {
        interface14.ui.callbacks.onRequestPlan = (planId, image, kernelPlanObjects) => {
          console.log(planId, image, kernelPlanObjects);
        };
        document.getElementById('trigger-request-plan')!.addEventListener('click', async () => {
          console.log('trigger-request-plan');
          await interface14.ui.triggerRequestPlan();
          console.log('trigger-request-done');
        });
      });
    }
  },
  methods: {
    getRubensApi(iframe: HTMLIFrameElement): Promise<ExposedApi> {
      const checkIfLoaded = (resolve: any) => {
        const rubensAPI = (iframe.contentWindow as any).RubensAPI;
        if (!rubensAPI) {
          setTimeout(checkIfLoaded.bind(this, resolve), 0);
        } else {
          resolve(rubensAPI);
        }
      };
      return new Promise((resolve) => {
        checkIfLoaded(resolve);
      });
    },
  },
});
